import { Component, OnInit } from '@angular/core';

import { User } from 'src/app/models/user';
import { NgForm } from '@angular/forms';
import { AuthService } from 'src/app/services/auth.service';
import { Router } from '@angular/router';
import { IpServiceService } from 'src/app/services/ip-service.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  public user: User;
  //ipAddress:string ='0.0.0.0';
  constructor(private authenticationService: AuthService, private router: Router,private ipService:IpServiceService) { }


  ngOnInit() {
    //this.getIP();

  }

  // getIP()
  // {
  //   this.ipService.getIPAddress().subscribe((res:any)=>{
  //     this.ipAddress=res.ip;
  //   });
  // }



  onSubmit(form: NgForm) {
    


    if (!form.valid) {
     
      return;
    }
    const username = form.value.username;
    const password = form.value.password;
    sessionStorage.clear();

    this.authenticationService.authenticationService(username, password);

    setTimeout(()=>{
      if (sessionStorage.getItem('role') === 'ROLE_ADMIN') {
        this.router.navigate(['/plan'])
      }
      else{
        this.router.navigate(['/home'])
      }
  
    },2000);

  }
}